export const answerValueInitList = (answers) => {
    return answers.map((answer) => [answer, false]);
};
const relativeDateOptions = ["today",];
export const answerValueInitRelativeDate = (answerValue) => {
    if (Array.isArray(answerValue)) {
        return {
            relative: false,
            relativeValue: null,
            absoluteValue: answerValue
        };
    }
    const { relative, value } = answerValue;
    return {
        relative: Boolean(relative),
        relativeValue: relative ? value : null,
        absoluteValue: !relative ? value : ["", "", ""],
    };
};
